<template>
  <div>
    <div v-for="(link, index) in links" :key="'project'+index">
      <project :link="link" />
    </div>
  </div>
</template>

<script>
import Project from './Project.vue'

export default {
  data () {
    return {
      links: [
        '/static/about.md',
        '/static/projects/startups.md',
        '/static/projects/encryptid.md',
        '/static/projects/autem.md',
        '/static/projects/university.md',
        '/static/projects/smartwatch.md',
        '/static/projects/serpent.md',
        '/static/projects/contracting.md',
        '/static/projects/rockstar.md',
        '/static/projects/personalprojects.md',
        '/static/projects/forge.md',
        '/static/projects/channellock.md',
        '/static/projects/more.md'
      ]
    }
  },
  components: {
    Project
  }
}
</script>



