<template>
  <div>
    <div class="card fluid">
      <div v-html="text" />
    </div>
  </div>
</template>

<script>
import marked from 'marked'

export default {
  name: 'project',
  props: ['link'],
  data () {
    return {
      text: ''
    }
  },
  async created () {
    this.text = await this.markdown()
  },
  methods: {
    async markdown () {
      const request = await fetch(this.link)
      const text = await request.text()
      return marked(text)
    }
  }
}
</script>



