      </router-link>
    </header>
    <div id="minimalistic">
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
.card {
  padding: 1ch 1ch 1ch 1ch;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

body {
    background-color: #f5f5f5;
    color: #002A15;
    line-height: 1.5;
}

#minimalistic {
    max-width: 70ch;
    padding: 2rem;
    margin: auto;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .25s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>



